import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';


const styles = {
    media: {
        height: 0,
        paddingTop: '56.25%', // 16:9
    },
    button: {
        justifyContent: 'flex-end',
    }
},
    normal = "/static/image/normal.png"


class ArcCard extends React.Component {

    render() {
        let { classes, source } = this.props;
        return (
            <Card className="card">
                <CardMedia
                    className={classes.media}
                    image={source.img || normal}
                    title={source.title}
                    component="a"
                    href={source.url}
                    target="_blank"
                />
                <CardContent>
                    <Typography className="card_title" gutterBottom variant="headline"> {source.title} </Typography>
                    {
                        source.list ?
                            <List className="_card_list" >
                                {
                                    source.list.map((item, key) => {
                                        return (
                                            <ListItem button key={key} component="a" href={item.url} target="_blank">
                                                <ListItemText primary={item.title} />
                                            </ListItem>
                                        )
                                    })
                                }
                            </List>
                            :
                            <Typography className="card_des" component="p">{source.des}</Typography>
                    }
                </CardContent>
                {
                    source.list ? "" :
                        <CardActions className={classes.button}>
                            <Button size="small" component="a" href={source.url} target="_blank"> take a look </Button>
                        </CardActions>
                }
            </Card>
        );

    }
}

ArcCard.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(ArcCard);